.form__question {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  &--active {
    .form__question-content {
      display: block;
    }
  }
}

.form__question-button {
  @include text ($font-14, $line-height-17, $regular);
  font-family: "Lato", "Arial", sans-serif;
  text-transform: uppercase;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 10px 10px 10px 20px;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &--active {
    svg {
      transform: rotate(45deg);
    }
  }
}

.form__question-content {
  display: none;
  width: 100%;
  min-height: 80px;
  resize: none;
  border: none;
  outline: none;
  overflow: hidden;
  padding: 5px 20px 10px 10px;

  &::placeholder {
    @include text ($font-12, $line-height-14, $regular);
    width: 520px;
  }

  &--open {
    display: block;
  }
}

.form__order-button {
  color: $white;
  filter: drop-shadow(0 10px 1px rgba(172, 172, 172, 0.25));
  margin: 0 auto;
  display: block;
}

.form__accordion {
  margin-bottom: 60px;
  width: 100%;
}

.form--askquestions {
  width: 550px;
  display: flex;
  flex-wrap: wrap;
}

.form--consultation {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  .form__button {
    margin: 0;
    padding: 15px;
    width: 270px;

    @media (max-width: 1440px) {
      width: 200px;
    }
  }

  input {
    @include text ($font-16, $line-height-19, $regular);
    padding: 10px;
    border: none;
    outline: none;
    width: 270px;
    border-bottom: 2px solid $orange;
    background-color: transparent;
    color: $white;

    &::placeholder {
      color: $medium-grey;
    }

    @media (max-width: 1440px) {
      width: 220px;
    }
  }
}

.form--callback,
.form--offer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  input {
    border: none;
    border-bottom: 1px solid $black;
    width: 270px;
    padding: 5px;
  }

  input::placeholder {
    text-transform: uppercase;
  }
}

.form__button {
  @include text ($font-16, $line-height-19, $bold);
  margin: 0 auto;
  margin-top: 63px;
}
