.specialists {
  background-image: url("../img/team-bg-desktop@1x.png");
  background-size: 100%;
  padding-top: 80px;
  padding-bottom: 80px;
  background-repeat: no-repeat;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/team-bg-desktop@2x.png");
  }

  @media (max-width: 1440px) {
    background-size: 140%;
    background-position: top left -400px;
  }

  h2 {
    @include reset;
    @include title ($font-36, $line-height-43, $bold, $white);
    margin-bottom: 140px;
    text-transform: uppercase;
    position: relative;

    &::after {
      @include title-after;
    }
  }

  p {
    @include reset;
  }

  button {
    display: block;
    margin: 0 auto;
    margin-top: 81px;
    border: 1px solid $white;
    color: $white;
  }
}

.specialists__list {
  @include list-reset;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.specialists__item {
  width: 270px;
  padding: 140px 10px 0;
  position: relative;
  color: $white;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-content: baseline;

  @media (max-width: 1440px) {
    width: 240px;
  }

  &:hover {
    &::after {
      filter: drop-shadow(0 10px 1px rgba(255, 172, 7, 0.25));
    }

    p:first-of-type {
      color: $orange;
    }
  }

  p:first-of-type {
    @include title ($font-16, $line-height-24, $bold, $white);
    text-transform: uppercase;
    min-height: 70px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 100px;
    display: block;
    border-radius: 20px;
    background-color: $white;
    box-shadow: 0 0 25px rgba(172, 172, 172, 0.25);
  }

  &::before {
    position: absolute;
    top: 8%;
    left: 50%;
    transform: translateX(-50%) scale(0.9);
    z-index: 10;
  }

  &--shield {
    &::before {
      content: url("../img/icon-shield.svg");
    }
  }

  &--maintenance {
    &::before {
      content: url("../img/icon-maintenance2.svg");
    }
  }

  &--engeneering {
    &::before {
      content: url("../img/icon-engeneering.svg");
    }
  }

  &--medal {
    &::before {
      content: url("../img/icon-medal.svg");
    }
  }
}
